import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
// import { useHistory } from 'react-router-dom';
import { addToCart, showCartDisplay } from '../actions/cartActions';
import { detailsProduct } from '../actions/productActions';

export default function BuyButton(props) {

  const dispatch = useDispatch();
  const productName = props.productName;
  const sizes = props.sizes;
  const [qty, setQty] = useState(1);
  const [size, setSize] = useState(sizes[0]);

  const productDetails = useSelector((state) => state.productDetails);
  const { loading, error, product } = productDetails;

  useEffect(() => {
    dispatch(detailsProduct(productName, size));
  }, [dispatch, productName, size]);

  const addToCartHandler = () => {
    dispatch(addToCart(product.id, qty));
    dispatch(showCartDisplay());
  }

  return (
    <div className="buy-container justify-content-center">
      <div className="buy-item">
        <div className="ml-auto">
          Talla
        </div>
        <div className="mr-auto">
          <select
            value={size}
            onChange={e => setSize(e.target.value)}
          >
            {
              sizes.map((x) => (
                <option key={x} value={x}>{x}</option>
              ))
            }
          </select>
        </div>
      </div>
      <div className="buy-item">
        <div className="ml-auto">Cant.</div>
        <div className="mr-auto">
          <select
            value={qty}
            onChange={e => setQty(e.target.value)}
          >
            {
              [...Array(10).keys()].map(x => (
                <option key={x + 1} value={x + 1}>{x + 1}</option>
              ))
            }
          </select>
        </div>
      </div>
      <div className="buy-item">
        <button onClick={addToCartHandler} className="primary block mx-auto">Agregar al carrito</button>
      </div>
    </div>
  )
}
