import React from 'react'
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/CR_Agua.png";

export default function CremaDeManosAgua() {
  return (
    <div>
      <ProductDescriptionTableJS />
      <div class="row product-landing bg-product-landing-agu"></div>

      <section class="product-info-section agua-section-bg">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="agu-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="agu-about-txt">
                  <b>UKemp <i>FUERZAS DE LA NATURALEZA</i></b> cuenta con el
                  equilibrio óptimo de ácidos grasos esenciales omega-3 y omega-6
                  del aceite de Cannabis sativa L., perfecto para la
                  revitalización de tu piel. Además, el aceite de Cannabis sativa
                  L. proporciona vitamina C, vitaminas del grupo B, vitaminas A y
                  E entre otros muchísimos activos poderosos, haciéndolo
                  extremadamente nutritivo e hidratante para proteger, mimar y
                  suavizar las manos sobrecargadas
                </p>
                <p id="agu-about-txt2">Eco Friendly</p>
                <p id="agu-about-txt3">Disponible en 30g.</p>
              </div>

              <div id="benefitsExpander" class="tab">
                <h4 id="agu-benefits" class="heading">
                  <span class="d-none d-sm-inline">Aceite de Semilla de</span>
                  Cannabis sativa L.
                </h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p><b>+ Efecto hidratante</b></p>
                <p>
                  El aceite de semilla de Cannabis sativa L.  es alto en ácidos omega 3, 6 y 9, todos los cuales ayudan a reparar la barrera de la piel y formar un sello sobre la piel para mantener la humedad atrapada en su interior. A diferencia de otros tipos de aceites, el aceite de semilla de Cannabis sativa L. hace que la piel se sienta tersa y natural en lugar de grasa.
                </p>
                <p><b>+ Fuerte antioxidante</b></p>
                <p>
                  El aceite contiene ácidos grasos cruciales como ácido linoleico y vitaminas que ayudan a proporcionar protección adicional de la piel y promover una regeneración celular más rápida que podría ayudar a reducir los signos de envejecimiento.
                </p>
                <p><b>+ Efecto antiinflamatorio</b></p>
                <p>
                  Aceite de semilla de Cannabis sativa L. ha demostrado beneficios antiinflamatorios cuando se aplica en la piel. Estudios recientes han sugerido que los ácidos omega-3 y omega-6 en el aceite podrían trabajar junto con los sistemas naturales del cuerpo para reducir mejor la inflamación de la piel. 
                </p>
              </div>

              <div id="directionsExpander" class="tab">
                <h4 id="agu-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="agu-directions-txt">
                Tomar una pequeña cantidad de crema en las manos y masajear hasta su total absorción. Utilizar tan a menudo como sea necesario.
                </p>
              </div>
            </div>

            <BuyButton productName="CremaDeManosAgua" sizes={["50g"]}></BuyButton>

          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-8 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="" />
                <h2 id="tie-puse" class="forces-font">
                  FUERZAS DE LA NATURALEZA
                </h2>
                <h2 id="tie-pname" class="agua forces-font">AGUA</h2>
                <p id="agu-summary">
                  Nuestra línea <b><i>FUERZAS DE LA NATURALEZA</i></b> para el
                  cuidados de manos esta formulada con aceite de Cannabis sativa
                  L., y una infusión de extractos naturales que la hacen el
                  tratamiento ideal para manos suaves y cuidadas todo el año.
                </p>
              </div>
              <div class="col-sm-4 order-1 order-sm-2 text-center">
                <img class="product-hc-image" src={productImage} alt="" />
              </div>
            </div>
            <div class="row key-item-display">
              <div
                class="margin-top-2 border-gradient gradient-agua key-item forces-font"
              >
                <div class="agua-tint"></div>
                <h4 class="agua">+ FUERZA DE AGUA</h4>
                <p class="">
                  El elemento Agua está directamente relacionado con el mundo de
                  los sentimientos y las emociones, los instintos, las
                  corazonadas, el inconsciente, lo psíquico, el pensamiento. El
                  elemento Agua se comunica con el mundo a través de la
                  creatividad, la mística, el arte, lo espiritual, las artes
                  esotéricas, la música y la imaginación.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>

    </div>
  )
}
