import React from 'react';
import logoWhite from '../img/general/ukemp-logo-white.png';
import swipe from '../img/general/swipe.png';
import { Link } from 'react-router-dom';
import Carousel from 'react-bootstrap/Carousel';


export default function HomePage() {

  return (
    <div>
      <Carousel className="carousel slide landing-carousel" interval={5000} controls={false} fade={true} pause={false}>
        <Carousel.Item className="carousel-cosm-img-1" interval={5000}>
          <img className="my-auto landing-logo d-none d-lg-block" src={logoWhite} alt="logo"/> 
        </Carousel.Item>
        <Carousel.Item className="carousel-cosm-img-2" interval={5000}>
          <div className="landing-text-right">
            <h1 className="sansita-swashed d-none d-lg-block">Naturally You</h1>
          </div>
        </Carousel.Item>
        <Carousel.Item className="carousel-cosm-img-3" interval={5000}>
          <div className="landing-text-left">
            <h1 className="sansita-swashed d-none d-lg-block">Naturally <br />Hemp</h1>
          </div>
        </Carousel.Item>
      </Carousel>

        <div className="swipe-section-img">
          <img src={swipe} alt=""/>
        </div>

        <div className="landing-content">
        <h1 className="text-center">Productos más vendidos</h1>
        <div className="product-banner">
          <div className="card-product" data-need="anti-frizz" data-category="shampoo">
            <Link to="/productos/shampoo-anti-frizz">
              <div className="container">
                <div className="front">
                  <div className="wrapper-img img-sfd-product"></div>
                </div>
                <div className="back">
                  <div className="wrapper-img img-sfd-back grey-scale"></div>
                </div>
              </div>
              <p id="c1-category">Shampoo</p>
              <h4 id="c1-name">Anti-Frizz</h4>
              <p id="c1-need">anti-frizz</p>
            </Link>
          </div>

          <div className="card-product" data-need="straightener" data-category="conditioner"> 
            <Link to="/productos/acondicionador-liso-4k">
              <div className="container">
                <div className="front">
                  <div className="wrapper-img img-cl4-product"></div>
                </div>
                <div className="back">
                  <div className="wrapper-img img-cl4-back grey-scale"></div>
                </div>
              </div>
              <p id="c7-category">Conditioner</p>
              <h4 id="c7-name">Liso 4K</h4>
              <p id="c7-need">Straightener</p>
            </Link>
          </div>

          <div className="card-product" data-need="silvering" data-category="mask">
            <Link to="/productos/mascara-capilar-silver">
              <div className="container">
                <div className="front">
                  <div className="wrapper-img img-mcs-product"></div>
                </div>
                <div className="back">
                  <div className="wrapper-img img-mcs-back grey-scale"></div>
                </div>
              </div>
              <p id="c12-category">Máscara</p>
              <h4 id="c12-name">RUBIO <i className="silver-text">SILVER</i></h4>
              <p id="c12-category">Efecto Platinado</p>
            </Link>
          </div>
        </div>

        <div className="text-center my-auto product-page-btn">
          <h4>
            <button style={{width: "200px;"}} onClick="window.open('/productos.html');">
              <Link to="/productos">Todos los productos</Link>
            </button>
          </h4>
        </div> 
      </div>
    </div>
  )
}
