import React from 'react';
import swipe from '../img/general/swipe.png';
import emailSVG from '../img/general/email.svg'


export default function ContactoPage() {
  return (
    <div>
      <section className="banner-landing background-full-page bg-contact-img">
        <div className="hero-title contact-title sansita-swashed d-none d-lg-block">
          <h1 id="title" className="text-white">CONTACTANOS</h1>
        </div>
      </section>

      <div className="swipe-banner-img">
        <img src={swipe} alt=""/>
      </div>

      <section style={{paddingTop: "50px"}}>
        <div className="row">
          <div className="col-2 d-none d-md-block"></div>
          <div className="col-12 col-md-8">
            <div className="form-container contact-container horizontally-center">
              <div className="row">
                <div className="float-left">
                  <img src={emailSVG} style={{height: "5rem"}} alt="email"/>
                </div>
                <div className="col">
                  <div style={{height: "80px"}}>
                    <h1 id="contact" style={{lineHeight: "80px"}}>Contacto</h1>
                  </div>
                </div>
              </div>

              <p>Gracias por visitarnos!</p>
              <p>
                Si deseas contactarte con nosotros, por favor completa el
                siguiente formulario
              </p>
              <p>*Los campos marcados con un asterisco son obligatorios</p>

              <div id="form-container">
                <form
                  id="contact-form"
                  className="needs-validation"
                  method="post"
                  novalidate
                >
                  <label id="email" for="form-email">Email*</label>
                  <input
                    type="text"
                    className="form-control"
                    id="form-email"
                    required
                  />

                  <div className="row">
                    <div className="col-sm-6" style={{paddingRight: "10px"}}>
                      <label id="first-name" for="form-first-name">Nombre*</label>
                      <input
                        type="text"
                        className="form-control"
                        id="form-first-name"
                        name="firstName"
                        required
                      />
                    </div>
                    <div className="col-sm-6">
                      <label id="last-name" for="form-last-name">Apellido*</label>
                      <input
                        type="text"
                        className="form-control"
                        id="form-last-name"
                        name="lastName"
                        required
                      />
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-sm-6" style={{paddingRight: "10px"}}>
                      <label id="phone-number" for="form-first-name"
                        >Número de teléfono</label
                      >
                      <input
                        type="text"
                        className="form-control"
                        id="form-first-name"
                        name="phoneNumber"
                      />
                    </div>
                    <div className="col-sm-6">
                      <label id="country" for="form-first-name">País</label>
                      <input
                        type="text"
                        className="form-control"
                        id="form-first-name"
                        name="country"
                      />
                    </div>
                  </div>

                  <label id="subject" for="form-subject">Sujeto*</label>
                  <input
                    type="text"
                    className="form-control"
                    id="form-subject"
                    name="subject"
                    required
                  />

                  <label id="message" for="form-message">Mesaje*</label>
                  <textarea
                    className="form-control"
                    id="form-message"
                    rows="5"
                    name="message"
                    required
                  ></textarea>
                  <div
                    className="d-flex justify-content-center"
                    style={{padding: "20px", height: "100px"}}
                  >
                    <button
                      className="align-self-center font-weight-bold"
                      type="submit"
                    >
                      Enviar
                    </button>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="col-2 d-none d-md-block"></div>
        </div>
      </section>
    </div>
  )
}
