import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { addToCart, removeFromCart, showCartDisplay, hideCartDisplay } from '../actions/cartActions';
import MessageBox from './MessageBox';
import getWindowDimensions from '../components/GetWindowDimensions';

export default function Cart(props) {
  
  const dispatch = useDispatch();
  const cart = useSelector((state) => state.cart);
  const { cartItems } = cart;
  const toggle = useSelector((state) => state.toggleCart.cartToggle);
  const history = useHistory();
  const { height, width } = getWindowDimensions();

  const [cartLeft, setCartLeft] = useState('');

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id));
  };

  const cartTabEvent = () => { 
    if (toggle) {
      dispatch(hideCartDisplay());
    } else {
      dispatch(showCartDisplay());
    }
  }

  useEffect(() => {
    !toggle? setCartLeft('100%') : ((width > 425)? setCartLeft('calc(100% - 400px'): setCartLeft('30px'));
  }, [toggle, width]);

  const checkoutHandler = () => {
    history.push('/proximamente/revisa');
  };

  return (
    <div 
      className="cart-wrapper"
      style={{left: cartLeft}}>
      <div 
        onClick={cartTabEvent} 
        className="cart-tab justify-content-center d-flex my-auto">
        <div>{cartItems.length > 0 && <span className="badge">{cartItems.length}</span>}</div>
        <div id="chevron" className={(toggle? 'rotate-left': 'rotate-right') + " fa fa-chevron-left my-auto text-white"}></div>
      </div>
      <h4 className="text-center">Carrito</h4>
      <hr></hr>
      {
        cartItems.length === 0 ? (
          <MessageBox>
            Carrito vacío <Link to="/productos">Ver Productos</Link>
          </MessageBox>
        ) : (
            <div className="cart-item-list">
              {cartItems.map((item) => (
                <div key={item.product} className="row cart-item">
                  <div className="col-3 text-center">
                    <img src={item.image} alt={item.name} className="small" />
                  </div>
                  <div className="col-9 my-auto text-center">
                    <Link className="width-100 item-name" to={`${item.productUrl}`}>{item.name} {item.size}</Link>
                    <div className="row item-actions">
                      <div className="col-4">
                        <select
                          value={item.qty}
                          onChange={(e) =>
                            dispatch(addToCart(item.id, Number(e.target.value)))
                          }
                        >
                          {[...Array(10).keys()].map((x) => (
                            <option key={x + 1} value={x + 1}>
                              {x + 1}
                            </option>
                          ))}
                      </select>
                      </div>
                      <div className="col-4">
                        <div>${item.price}</div>
                      </div>
                      <div className="col-4">
                        <button
                          type="button"
                          onClick={() => removeFromCartHandler(item.product)}
                          className="fa fa-trash warning"
                        />
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        <div className="checkout text-center">
          <h4>
            Total ({cartItems.reduce((a, c) => a + Number(c.qty), 0)} productos) : $
            {cartItems.reduce((a, c) => a + c.price * c.qty, 0)}
          </h4>
          <button
              type="button"
              onClick={checkoutHandler}
              className="primary block"
              disabled={cartItems.length === 0}
            >
              Pasar por la caja
            </button>
        </div>
    </div>
  )
}
