import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../img/general/ukemp-logo.png';
import penStroke from '../img/general/pen-stroke.png';
import $ from 'jquery';

export default function Nav() {

  $( "#nav-logo-img" ).on('click', () => {
    window.location.href = '/index.html';
  });
  // push content down with nav-underbelly
  // const intialNavHeight = document.getElementById("nav").offsetHeight;

  var prevScrollpos = window.pageYOffset;
  window.onscroll = () => {
    var currentScrollPos = window.pageYOffset;
    if (prevScrollpos > currentScrollPos) {
      document.getElementById("nav").style.top = "0";
    } else {
      document.getElementById("nav").style.top = "-130px";
    }
    prevScrollpos = currentScrollPos;
  }

  return (
    <div>
      <nav id="nav" className="navbar-dark">
  
        <div className="text-right nav-message">
          <Link className="sansita-swashed" style={{color: "rgb(19 106 64)"}}>Se parte de la revolución natural</Link>
          <img src={penStroke} alt="" />
        </div>
          
        <div className="main-nav d-flex justify-content-between" style={{width: "100%;"}}>
          <Link to="/" style={{padding:0}}>
            <img id="nav-logo-img" className="nav-logo" src={logo} alt="logo"/>
          </Link>
          
          <div className="d-none d-xl-block navbar-center mx-auto">
            <Link id="products" to="/productos">PRODUCTOS</Link>
            <Link id="about" to="/sobre-nosotros">SOBRE NOSOTROS</Link>
            <Link id="cannabis" to="/cannabis-por-el-planeta">CANNABIS POR EL PLANETA</Link>
            <Link id="ecology" to="/cultura-ecologica">CULTURA ECOLOGICA</Link>
            <Link id="contact" to="/contacto">CONTACTANOS</Link>
          </div>
          <div className="d-xl-none col-1 my-auto">
            <div className="dropdown float-right">
              <button className="btn btn-primary dropdown-toggle" type="button" data-toggle="dropdown" style={{background: "rgb(19, 106, 64)", border: "0px; filter: invert(1)"}}>
                <span className="navbar-toggler-icon" />
              </button>
              <ul className="dropdown-menu text-center">
                <li><Link id="d-products" to="/productos">PRODUCTOS</Link></li>
                <li><Link id="d-about" to="/sobre-nosotros">SOBRE NOSOTROS</Link></li>
                <li><Link id="d-cannabis" to="/cannabis-por-el-planeta">CANNABIS POR EL PLANETA</Link></li>
                <li><Link id="d-ecology" to="/cultura-ecologica">CULTURA ECOLOGICA</Link></li>
                <li><Link id="d-contact" to="/contacto">CONTACTANOS</Link></li>
              </ul>
            </div>
          </div>      
        </div>
      </nav>
      <div style={{height: "120px", width: "100%"}}></div>
    </div>
  )
}
