import React from 'react';
import swipe from '../img/general/swipe.png';
import image1 from '../img/c4tw/c4tw-1.jpeg';
import image3 from '../img/c4tw/c4tw-3.jpeg';
import image4 from '../img/c4tw/c4tw-4.jpeg';
import image5 from '../img/c4tw/c4tw-5.jpeg';



export default function CannabisPorElPlanetaPage() {
  return (
    <div>
      <section className="banner-landing background-full-page bg-c4tw-img">
        <div className="hero-title c4tw-title sansita-swashed d-none d-lg-block">
          <h1 id="title" className="">CANNABIS POR</h1> 
          <h1 id="title" className="">EL PLANETA</h1>
        </div>
      </section>

      <div className="swipe-banner-img">
        <img src={swipe} alt=""/>
      </div>

      <section className="article-content" style={{paddingTop: "120px"}}>
        <div className="content-page">
          <h1 className="sansita-swashed d-lg-none">CANNABIS POR EL PLANETA</h1>
          <hr />
          <h4 id="title sansita-swashed">El Aceite de semilla de Cannabis sativa L. es bueno para el planeta! </h4>
          <hr />
          <p id="text-0">
            Desde limpiar el aire hasta regenerar el suelo y proporcionar un hábitat para la vida silvestre, la planta de Cannabis sativa es buena para el planeta y tiene muchos beneficios ambientales, simples y llanos. Y cuando tus amigos te preguntan exactamente cómo o por que el Cannabis  está salvando el mundo, entonces puedes citar cualquiera de las razones que hemos enumerado para ti aquí.
          </p>
        </div>
      </section>

      <div className="content-banner bg-static bg-c4tw-banner">
        <div className="overlay-banner"></div> 
      </div>

      <section className="article-content">
        <div className="content-page">
          
          <h4 id="subtitle-1">1. El Cannabis respira CO<sub>2</sub></h4>
          <img className="img-r image-rounding" src={image1} alt=""/>
          <p id="text-1">
            El Cannabis es básicamente el purificador de la naturaleza. La planta captura rápidamente el dióxido de carbono de la atmósfera y hace que lo que respiramos sea mucho más limpio. De hecho, por cada tonelada de Cannabis producida, 1,63 toneladas de carbono se elimina del aire (lo que hace que el Cannabis sea un secuestrador mucho más eficaz de dióxido de carbono que los árboles). 
          </p>
          <h4 id="subtitle-2">2. El Cannabis regenera el suelo</h4>
          <p id="text-2">
            El tallo y las hojas de la planta de Cannabis están repletos de nutrientes. A medida que la planta madura y la semilla crece, todo ese material de la hoja cae y se descompone reponiendo el suelo aportando todas sus al siguiente cultivo. Después de la cosecha, los restos de la planta pueden ser devueltos al suelo para un mayor rendimiento al año siguiente.
          </p>

          <h4 id="subtitle-3">3. El Cannabis apoya la agricultura sostenible</h4>
          <p id="text-3">
            Un aspecto importante, la agricultura sostenible, trata de rotar los cultivos según la temporada con el fin de mantener los nutrientes del suelo. Debido a que el Cannabis es un cultivo anual que crece dentro de sólo cuatro meses de ser plantado, es un candidato ideal para la rotación y genera un beneficio ambiental maravilloso. Los agricultores de todo el mundo se alegran porque esto significa un suelo más rico y limpio y un mayor rendimiento de los cultivos.
          </p>      
        </div>
      </section>

      <div className="content-banner bg-static bg-c4tw-banner">
        <div className="overlay-banner"></div> 
      </div>

      <section className="article-content">
        <div className="content-page">
          <h4 id="subtitle-4">4. Ayuda contra los pesticidas </h4>
          <p id="text-4">
            A diferencia de otras fibras naturales como el algodón o el lino, otro beneficio ambiental del Cannabis es que no requiere pesticidas o herbicidas para crecer. Se ha demostrado que la exposición a pesticidas causa problemas ambientales como la contaminación del agua, y también se ha relacionado con problemas de salud. ¿Una solución potencial? Utilizar el Cannabis en todo el mundo y así reducir la cantidad de toxinas y contaminantes en nuestro aire y agua!
          </p>

          <img className="img-l image-rounding" src={image3} alt=""/>
          <h4 id="subtitle-5">5. El Cannabis previene la erosión del suelo</h4>
          <p id="text-5">
            Las raíces de la planta de Cannabis crecen fuertes y hasta alrededor de 3 metros de profundidad! Este tipo de redes radiculares pueden ayudar a mantener el suelo unido y prevenir la erosión, que es uno de los mayores problemas a los que se enfrentan los agricultores hoy en día. En algunos casos, el beneficio ambiental del Cannabis es que incluso ha restaurado el suelo que ya estaba dañado. 
          </p>

          <h4 id="subtitle-6">6. El Cannabis necesita poca agua</h4>
          <p id="text-6">
            El Cannabis tiene la capacidad milagrosa de irrigarse naturalmente, lo que significa que requiere muy poca agua para crecer. Esto hace la diferencia por ejemplo entre la leche de Cannabis y otras opciones como la leche de soja o la almendra, y otras plantas de fibra natural como el algodón que son muy sedientos de hecho.
          </p>

          <img className="img-r image-rounding" src={image4} alt=""/>
          <h4 id="subtitle-7">7. Nada se desperdicia con el Cannabis</h4>
          <p id="text-7">
            El Cannabis industrial se puede utilizar en unos 50.000 productos diferentes en un amplio espectro de industrias: desde textiles hasta productos alimenticios, materiales de construcción para bioplásticos, nutracéuticos a nanomateriales, etanol a ropa de cama de animales. Absolutamente nada se desperdicia con esta planta!
          </p>  

        </div>
      </section>

      <div className="content-banner bg-static bg-c4tw-banner">
        <div className="overlay-banner"></div> 
      </div>

      <section className="article-content">
        <div className="content-page">
          <h4 id="subtitle-8">8. El Cannabis absorbe metales tóxicos</h4>
          <p id="text-8">
            Hemos visto cómo el Cannabis puede limpiar el aire y reponer el suelo, pero también puede eliminar las toxinas dañinas absorbiéndolas. Famosamente, la planta fue utilizada después del desastre nuclear en Chernóbil para eliminar el estroncio radiactivo y el cesio, e incluso ha sido considerada para eliminar la radiación de Fukushima. 
          </p>

          <img className="img-l image-rounding" src={image5} style={{padding:0, maxWidth: "60%", marginTop: "15px"}} alt=""/>

          <h4 id="subtitle-9">9. El Cannabis proporciona un hábitat para la vida silvestre</h4>
          <p id="text-9">
            El punto número nueve está aliado con los pájaros y las abejas. Nuestro segundo último beneficio ambiental del Cannabis es que sus plantas crecen hasta tres pies de altura, lo que las convierte en un excelente escondite para la vida silvestre. Ah y cuando florecen las flores de Cannabis hay una buena fuente de polen para las abejas.
          </p>

          <h4 id="subtitle-10">10. El Cannabis puede ayudar en la lucha contra la deforestación</h4>
          <p id="text-10">
            Científicos de todo el mundo ahora creen que dentro de 100 años no habrá selva tropical. Pero hay esperanza en el Cannabis. Mientras que los árboles tardan años en madurar, el Cannabis se puede cultivar en sólo cuatro meses. Por lo tanto, la planta podría reemplazar los árboles como fuente de materia prima para el papel. El Cannabis es bueno para el planeta!
          </p>
          <div className="stretch"></div>
        </div>
      </section>
    </div>
  )
}
