import React from 'react'
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/srb_400.png";

export default function ShampooRepairBomba() {
  return (
    <div>
      <ProductDescriptionTableJS />
      <div class="row product-landing bg-product-landing-srb"></div>

      <section class="product-info-section">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="srb-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="srb-benefits-txt1">
                  Estas proteínas, al igual que la biotina y el zinc, son
                  esenciales para la piel y el cabello, pero con el paso del
                  tiempo decrece la producción de folículos pilosos y fibras que
                  le dan forma.
                </p>
                <p id="srb-benefits-txt2">
                  Como consecuencia de esta falta, nuestro pelo se apaga, se
                  vuelve débil, quebradizo y sin brillo. Por ello, utilizar UKemp
                  Bomba de Proteínas hará que tu cabello se regenere y fortalezca,
                  manteniéndose fuerte y resistente, como nunca lo viste.
                </p>
                <p id="srb-about-txt3">Para uso diario – Todo tipo de cabellos</p>
                <p id="srb-about-txt4">Disponible en 400 mL y 650mL</p>
                <p id="srb-about-txt5" class="text-center">
                  <i>Calidad profesional, a tu alcance</i>
                </p>
              </div>

              <div id="benefitsExpander" class="tab">
                <h4 id="srb-benefits" class="heading">
                  <span class="d-none d-sm-inline">Aceite de Semilla de</span>
                  Cannabis sativa L.
                </h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p id="srb-directions-txt1">
                  El aceite de cáñamo con el que formulamos se produce mediante
                  prensado en frio de las semillas de cáñamo para conservar todos
                  sus valiosos componentes intactos. Es considerado el más
                  nutritivo de todos los aceites disponibles.
                </p>
                <p id="srb-directions-txt2">
                  La composición de ácidos grasos del aceite de semilla de cáñamo
                  alivia el cuero cabelludo de la sequedad y estimula su flujo
                  sanguíneo, permitiendo la llamada reposición de lípidos
                  fortaleciendo el cabello para darle el poder de combatir el
                  estrés diario.
                </p>
                <p id="srb-directions-txt3">
                  El aceite de cáñamo garantiza un equilibrio estructural
                  perfecto, resultado: un cabello más fuerte y reestructurado
                  desde la raíz a las puntas!
                </p>
              </div>

              <div id="directionsExpander" class="tab">
                <h4 id="srb-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="srb-directions-txt">
                  Agitar antes de usar. Masajear en el cabello mojado. Enjuague
                  totalmente.
                </p>
              </div>
            </div>

            <BuyButton productName="ShampooRepairBomba" sizes={["400ml", "650ml"]}></BuyButton>

          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-8 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="UKemp" />
                <h1 id="srb-pname">SHAMPOO <i>Bomba 𝛿e Proteinas</i></h1>
                <h4 id="srb-puse">Reparación Total</h4>
                <p id="srb-summary">
                  <b>UKemp <i>SHAMPOO Bomba 𝛿e Proteinas</i></b> con su fórmula
                  con aceite de Cannabis, aminoácidos de colágeno y péptidos de
                  Keratina, nutre tu cabello desde la raíz, para que crezca largo
                  y fuerte.
                </p>
              </div>
              <div class="col-sm-4 order-1 order-sm-2 text-center">
                <img class="product-image" src={productImage} alt="product"/>
              </div>
            </div>

            <div class="row key-item-display">
              <div class="col-xl-6 key-item-left-col">
                <div class="key-item-left my-auto">
                  <h4 id="srb-ingredient1">+ KERATINA</h4>
                  <p id="srb-ingredient1-txt">
                    Repara las fibras del cabello, lo protege de agentes externos
                    y lo nutre desde la raíz dándole un aspecto totalmente
                    regenerado.
                  </p>
                </div>
              </div>
              <div class="col-xl-6 key-item-right-col">
                <div class="key-item-right my-auto">
                  <h4 id="srb-ingredient2">+ COLAGENO</h4>
                  <p id="srb-ingredient2-txt">
                    El colágeno da fuerza, suavidad y forma al cabello, dado que
                    es fuente de aminoácidos que el organismo necesita para
                    producir keratina.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
