import React from 'react'
import swipe from '../img/general/swipe.png';
import image1 from '../img/ec/ec-1.jpeg';
import image2 from '../img/ec/ec-2.jpeg';


export default function CulturaEcologicaPage() {
  return (
    <div>
      <section className="banner-landing background-full-page bg-ec-img">
        <div className="hero-title ce-title sansita-swashed d-none d-lg-block text-center">
          <h1 id="title" className="">CULTURA ECOLOGICA</h1>
        </div>
      </section>

      <div className="swipe-banner-img">
        <img src={swipe} alt="" />
      </div>

      <section className="article-content" style={{paddingTop: "120px"}}>
        <div className="content-page">
          <h1 id="title">¿Qué es Eco-friendly?</h1>
          <hr />
          <img className="img-r image-rounding" src={image1} alt="" />
          <p id="text-0a">
            El término <i>eco-friendly</i> se puede traducir literalmente del inglés como ‘respetuoso con el medio ambiente. Proviene de la palabra <i>Eco</i>, abreviatura de <i>ecología</i>, que es la ciencia que estudia los seres vivos y las relaciones que mantienen entre sí y con el propio medio. Y la palabra <i>Friendly</i>, que significa <i>amigable</i>, o <i>respetuoso de</i>.
          </p>
          <p id="text-0b">
            El verdadero significado de <i>eco-friendly</i> engloba realizar acciones que conduzcan a una vida más saludable y responsable con el planeta, los animales y  las personas. Por eso en <b className="ukemp-green">UKemp</b>, utilizamos prácticas que ayuden a conservar los recursos limitados y el hábitat de otros seres vivos, que eviten contaminar el aire, el agua y la tierra, reutilizando y reciclando todo lo que podemos, y compostamos lo que no podemos.
          </p>

          <h4 id="subtitle-1">¿Cómo impacta la basura que acumulamos en nuestro entorno?</h4>
          <p id="text-1a">
            En el mundo se genera cada vez más basura. Según un informe del Banco Mundial, los desechos producidos en el planeta en 2016 alcanzaron los 2 billones de toneladas, y esta cifra podría dispararse hasta los 3.4 billones en el año 2050, es decir, casi un 70% más de basura en solo treinta años. Vivimos en la era del usar y tirar y no sabemos qué hacer con tantos residuos.
          </p>
          <p id="text-1b">
            Muchos de los desechos que generamos acaban teniendo una segunda vida gracias a la ayuda de los ciudadanos que separan en casa aquellos residuos que pueden ser reciclados y a la actuación eficaz de la Administración que los trata en sus plantas de reciclaje.
          </p>
          <p id="text-1c">
            Para nosotros en <b className="ukemp-green">UKemp</b> es imprescindible comprender la fragilidad del entorno, que necesita muy poco para dañarse y mucho para recuperarse. Para ello desde <b className="ukemp-green">UKemp</b> nos comprometemos a:
          </p>
          <img className="img-l image-rounding" src={image2} alt="" />
          <ul>
            <li>Emplear artículos biodegradables.</li>
            <li>Utilizar materias primas eco-friendly.</li>
            <li>Reciclar de forma efectiva.</li>
            <li>Generar menos residuos mediante la reutilización, la reparación y la reducción.</li>
            <li>Apostar por las energías renovables.</li>
            <li>Reducir el consumo de agua y electricidad.</li>
            <li>No emplear productos de limpieza que puedan dañar el medio ambiente.</li>
          </ul>
          <div className="stretch"></div>
        </div>

      </section>
    </div>
  )
}
