import React from 'react'
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/mc_200.png";

export default function MascaraCapilar() {
  return (
    <div>
      <ProductDescriptionTableJS />
      <div class="row product-landing bg-product-landing-mc"></div>

      <section class="product-info-section">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="mc-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="mc-about-txt1">
                  <b>UKemp <i>MÁSCAЯA CAPILAЯ</i></b>, es un remedio altamente
                  nutritivo para el cabello reseco y sensibilizado, nutriendo
                  intensamente. Formulado a base de aceite de Cannabis virgen,
                  considerado el más nutritivo de todos los aceites disponibles.
                </p>
                <p id="mc-about-txt4">Para uso diario – Todo tipo de cabellos</p>
                <p id="mc-about-txt5">Disponible en 200 mL</p>
                <p id="mc-about-txt6" class="text-center"><i>Calidad profesional, a tu alcance</i></p>
              </div> 
              <div id="benefitsExpander" class="tab">
                <h4 id="ctd-benefits" class="heading"><span class="d-none d-sm-inline ">Aceite de Semilla de</span> Cannabis sativa L.</h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p id="mc-directions-txt1">
                  El aceite de cáñamo con el que formulamos se produce mediante prensado en frio de las semillas de cáñamo para conservar todos sus valiosos componentes intactos. Es considerado el más nutritivo de todos los aceites disponibles. 
                </p>
                <p id="mc-directions-txt2">
                  La composición de ácidos grasos del aceite de semilla de cáñamo alivia el cuero cabelludo de la sequedad y estimula su flujo sanguíneo, permitiendo la llamada reposición de lípidos fortaleciendo el cabello para darle el poder de combatir el estrés diario.
                </p>
                <p id="mc-directions-txt3">
                  El aceite de cáñamo garantiza un equilibrio estructural perfecto, resultado: un cabello más fuerte y reestructurado desde la raíz a las puntas!
                </p>
              </div>
              
              <div id="directionsExpander" class="tab">
                <h4 id="mc-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="mc-directions-txt">1.	Lava tu cabello como de costumbre, y sécalo con una toalla sin frotar.</p> 
                <p>2.	Aplica la máscara mecha por mecha poco a poco. Ayúdate de un peine para peinarlo bien y que penetre.</p>
                <p>3.	Deja reposar la máscara  por lo menos 10 minutos, sin ningún límite de tiempo. Cuanto más tiempo, mejor, y si puedes, aplícate una toalla caliente, ya que el calor multiplica los beneficios de la máscara.</p>
                <p>4.	Pasado este tiempo, elimina los restos de la máscara enjuagando el cabello con agua tibia.</p>
                <p>TIP: Puedes dejártelo toda la noche usando una gorra de baño convencional.</p>
              </div>
            </div>

            <BuyButton productName="MascaraCapilar" sizes={["200ml"]}></BuyButton>

          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-7 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="UKemp" />             
                <h1 id="mc-pname"><i>MÁSCAЯA CAPILAЯ</i></h1>
                <h4 id="mc-puse">Revitalizante + Reparado</h4>
                <p id="mc-summary">
                  <b>UKemp <i>MÁSCAЯA CAPILAЯ </i></b> regenera y reestructura el
                  cabello, y protege el cuero cabelludo. Con acción suavizante actúa
                  dejando el cabello sedoso y brillante, nutre intensamente sin
                  apelmazar. Ideal para cabellos secos y dañados.
                </p>
              </div>
              <div class="col-sm-5 order-1 order-sm-2 text-center">
                <img class="product-image" style={{marginTop:0}} src={productImage} alt="product" />
              </div>
            </div>
            <div class="row key-item-display">
              <div class="col-xl-6 key-item-left-col">
                <div class="key-item-left my-auto">
                  <h4 id="mc-ingredient1">+ KERATINA</h4>
                  <p id="mc-ingredient1-txt">Repara las fibras del cabello, lo protege de agentes externos y lo nutre desde la raíz dándole un aspecto totalmente regenerado.</p>
                </div>
              </div>
              <div class="col-xl-6 key-item-right-col">
                <div class="key-item-right my-auto">
                  <h4 id="mc-ingredient2">+ MANZANILLA</h4>
                  <p id="mc-ingredient2-txt">Camomile promueve un cabello saludable y brillante por sus propiedades antioxidantes, calmantes y antinflamatorias y sus compuestos activos permiten aliviar la picazón del cuero cabelludo seco que puede conducir a la caspa.</p>
                </div>
              </div>            
            </div>
          </div>
        </div>      
      </section>
    </div>
  )
}
