import React from 'react'
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/std_400.png";

export default function ShampooDetox() {
  return (
    <div>
      <ProductDescriptionTableJS />
      <div class="row product-landing bg-product-landing-std "></div>

      <section class="product-info-section">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="std-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="std-about-txt">
                  Enriquecido con aceite de semilla de Cannabis sativa L. y extracto de
                  Aloe vera, libera a tu cabello de impurezas no deseadas y crea la base
                  perfecta para una regeneración inmediata. Puede ser utilizado todos
                  los días y es apto tanto para cabellos normales como grasos.                
                </p>
                <p id="std-about-txt2">Para uso diario – Todo tipo de cabellos</p>
                <p id="std-about-txt3">Disponible en 400 mL y 650mL</p>
                <p id="std-about-txt4" class="text-center"><i>Calidad profesional, a tu alcance</i></p>
              </div>
              
              <div id="benefitsExpander" class="tab">
                <h4 id="std-benefits" class="heading"><span class="d-none d-sm-inline ">Aceite de Semilla de</span> Cannabis sativa L.</h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p id="std-directions-txt1">
                  El aceite de cáñamo con el que formulamos se produce mediante prensado en frio de las semillas de cáñamo para conservar todos sus valiosos componentes intactos. Es considerado el más nutritivo de todos los aceites disponibles. 
                </p>
                <p id="std-directions-txt2">
                  La composición de ácidos grasos del aceite de semilla de cáñamo alivia el cuero cabelludo de la sequedad y estimula su flujo sanguíneo, permitiendo la llamada reposición de lípidos fortaleciendo el cabello para darle el poder de combatir el estrés diario.
                </p>
                <p id="std-directions-txt3">
                  El aceite de cáñamo garantiza un equilibrio estructural perfecto, resultado: un cabello más fuerte y reestructurado desde la raíz a las puntas!
                </p>
              </div>
              <div id="directionsExpander" class="tab">
                <h4 id="std-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="std-directions-txt">
                  Agitar antes de usar. Masajear en el cabello mojado. Enjuague totalmente.
                </p>
              </div>
            </div>

            <BuyButton productName="ShampooDetox" sizes={["400ml", "650ml"]}></BuyButton>


          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-8 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="UKemp" />              
                <h1 id="std-pname">SHAMPOO <i>DETØX</i></h1>
                <h4 id="std-puse">Libre de Impurezas</h4>
                <p id="std-summary">
                  Inspirado en los tratamientos de salón, <b>UKemp <i>SHAMPOO DETØX </i></b>
                  elimina los residuos que se acumulan durante el día como humo,
                  polución y sudor, dejando el cabello purificado y nutrido. 
                </p>
              </div>
              <div class="col-sm-4 order-1 order-sm-2 text-center">
                <img class="product-image" src={productImage} alt="product" />
              </div>
            </div>
            <div class="row key-item-display">
              <div class="col-xl-6 key-item-left-col">
                <div class="key-item-left my-auto">
                  <h4 id="std-ingredient1">+ KERATINA</h4>
                  <p id="std-ingredient1-txt">Repara las fibras del cabello, lo protege de agentes externos y lo nutre desde la raíz dándole un aspecto totalmente regenerado.</p>
                </div>
              </div>
              <div class="col-xl-6 key-item-right-col">
                <div class="key-item-right my-auto">
                  <h4 id="sl4-ingredient2">+ ALOE VERA</h4>
                  <p id="sl4-ingredient2-txt">
                    El Aloe vera se ha utilizado durante siglos para tratar la
                    caída del cabello y balancear el cuero cabelludo, dejando tu
                    cabello suave, brillante y sin frizz! Además, ayuda a reducir
                    la caspa y desbloquear los folículos pilosos que pueden estar
                    bloqueados por el exceso de impurezas.
                  </p>
                </div>
              </div>            
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
