import $ from 'jquery';

export default function ProductDescriptionTableJS() {
  $(function () {
    $("#aboutExpandable").show();
    $("#nav-placeholder").load("/components/nav.html", function () {});
    $("#footer-placeholder").load("/components/footer.html");
  
    $("#aboutExpander").click(function () {
      // minimise other tabs
      $("#benefitsExpandable").hide("fast");
      $("#directionsExpandable").hide("fast");
      // expand section
      $("#aboutExpandable").show("fast");
      $("#expandIconAbout").text(function () {
        return "-";
      });
      $("#expandIconBenefits").text(function () {
        return "+";
      });
      $("#expandIconDirections").text(function () {
        return "+";
      });
    });
  
    $("#benefitsExpander").click(function () {
      // minimise other tabs
      $("#aboutExpandable").hide("fast");
      $("#directionsExpandable").hide("fast");
      // expand section
      $("#benefitsExpandable").show("fast");
      $("#expandIconAbout").text(function () {
        return "+";
      });
      $("#expandIconBenefits").text(function () {
        return "-";
      });
      $("#expandIconDirections").text(function () {
        return "+";
      });
    });
  
    $("#directionsExpander").click(function () {
      // minimise other tabs
      $("#aboutExpandable").hide("fast");
      $("#benefitsExpandable").hide("fast");
      // expand section
      $("#directionsExpandable").show("fast");
      $("#expandIconAbout").text(function () {
        return "+";
      });
      $("#expandIconBenefits").text(function () {
        return "+";
      });
      $("#expandIconDirections").text(function () {
        return "-";
      });
    });
  });

  return null;
}

