import Axios from 'axios';
import { CART_ADD_ITEM, CART_REMOVE_ITEM, SHOW_CART, HIDE_CART } from '../constants/cartConstants';

export const addToCart = (productId, qty) => async (dispatch, getState) => {
  const { data } = await Axios.get(`/api/product/${productId}`);
  dispatch({
    type: CART_ADD_ITEM,
    payload: {
      id : data.id,
      name: data.name,
      image: data.image,
      price: data.price,
      product: data.productName,
      productUrl: data.productUrl,
      size: data.size,
      qty,
    },
  });
  localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems));
};

export const removeFromCart = (productId) => (dispatch, getState) => {
  dispatch({ type: CART_REMOVE_ITEM, payload: productId });
  localStorage.setItem('cartItems', JSON.stringify(getState().cart.cartItems));
};

export const showCartDisplay = () => (dispatch, getState) => {
  dispatch({type: SHOW_CART, payload: true});
  localStorage.setItem('cartToggle', JSON.stringify(getState().toggleCart));
};

export const hideCartDisplay = () => (dispatch, getState) => {
  dispatch({ type: HIDE_CART, payload: false });
  localStorage.setItem('cartToggle', JSON.stringify(getState().toggleCart));
};