import React from 'react';
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/cfd_400.png";

export default function AcondicionadorFrizzDefense() {
  return (
    <div>
      <ProductDescriptionTableJS />
      <div class="row product-landing bg-product-landing-cfd">
        <div class="col-6 d-none d-lg-block"></div>
        <div class="col-lg-6 col-12"></div>
      </div>

      <section class="product-info-section">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="cfd-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="cfd-about-txt1">
                  <b
                    >UKemp 
                    <i
                      > ACONDICIONADOR <span class="no-break">#Anti-Frizz </span></i
                    ></b
                  >
                  está dotado con extracto de Manzanilla y un complejo de activos
                  que evita el efecto frizz, facilita el peinado, y ofrece un
                  cabello manejable, acabado liso, resistente a la humedad y
                  duradero.
                </p>
                <p id="cfd-about-txt2">Para uso diario – Todo tipo de cabellos</p>
                <p id="cfd-about-txt3">Disponible en 400 mL</p>
                <p id="cfd-about-txt4" class="text-center">
                  <i>Calidad profesional, a tu alcance</i>
                </p>
              </div>

              <div id="benefitsExpander" class="tab">
                <h4 id="cfd-benefits" class="heading">
                <span class="d-none d-sm-inline ">Aceite de Semilla de</span> Cannabis sativa L.
                </h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p id="cfd-directions-txt1">
                  El aceite de cáñamo con el que formulamos se produce mediante
                  prensado en frio de las semillas de cáñamo para conservar todos
                  sus valiosos componentes intactos. Es considerado el más
                  nutritivo de todos los aceites disponibles.
                </p>
                <p id="cfd-directions-txt2">
                  La composición de ácidos grasos del aceite de semilla de cáñamo
                  alivia el cuero cabelludo de la sequedad y estimula su flujo
                  sanguíneo, permitiendo la llamada reposición de lípidos
                  fortaleciendo el cabello para darle el poder de combatir el
                  estrés diario.
                </p>
                <p id="cfd-directions-txt3">
                  El aceite de cáñamo garantiza un equilibrio estructural
                  perfecto, resultado: un cabello más fuerte y reestructurado
                  desde la raíz a las puntas!
                </p>
              </div>

              <div id="directionsExpander" class="tab">
                <h4 id="cfd-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="cfd-directions-txt">
                  Agitar antes de usar. Aplicar haciendo masajes en el cabello
                  mojado. Dejar actuar 1 a 2 minutos y enjuagar con abundante agua
                  hasta que la misma se escurra sin color alguno. Puede aumentar
                  el tiempo de aplicación si se desea un resultado más intenso.
                </p>
              </div>
            </div>

            <BuyButton productName="AcondicionadorAntiFrizz" sizes={["400ml"]}></BuyButton>

          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-8 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="UKemp" />
                <h1 id="cfd-pname">
                  ACONDICIONADOR <i class="no-break">#Anti-Frizz</i>
                </h1>
                <h4 id="cfd-puse">Frizz-Defense</h4>
                <p id="cfd-summary">
                  <b
                    >UKemp 
                    <i
                      > ACONDICIONADOR <span class="no-break">#Anti-Frizz </span></i
                    ></b
                  >
                  protege a tu cabello contra la humedad, favoreciendo al
                  desenredado y ayudando a realinear la fibra capilar.
                </p>
              </div>
              <div class="col-sm-4 order-1 order-sm-2 text-center">
                <img class="product-image" src={productImage} alt="#Anti-Frizz Bottle" />
              </div>
            </div>
            <div class="row key-item-display">
              <div class="col-xl-6 key-item-left-col">
                <div class="key-item-left my-auto">
                  <h4 id="cfd-ingredient1">+ KERATINA</h4>
                  <p id="cfd-ingredient1-txt">
                    Repara las fibras del cabello, lo protege de agentes externos
                    y lo nutre desde la raíz dándole un aspecto totalmente
                    regenerado.
                  </p>
                </div>
              </div>
              <div class="col-xl-6 key-item-right-col">
                <div class="key-item-right my-auto">
                  <h4 id="cfd-ingredient2">+ MANZANILLA</h4>
                  <p id="cfd-ingredient2-txt">
                    La manzanilla promueve un cabello saludable y brillante por
                    sus propiedades antioxidantes, calmantes y antinflamatorias.
                    Sus compuestos activos permiten aliviar la picazón del cuero
                    cabelludo seco que puede conducir a la caspa.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
