import { CART_ADD_ITEM, CART_REMOVE_ITEM, SHOW_CART, HIDE_CART, } from '../constants/cartConstants';

export const cartReducer = (state = { cartItems: [] }, action) => {
  switch (action.type) {
    case CART_ADD_ITEM:
      const item = action.payload;
      const existItem = state.cartItems.find((x) => x.product === item.product);
      if (existItem) {
        return {
          ...state,
          cartItems: state.cartItems.map((x) =>
            x.product === existItem.product ? item : x,
          ),
        };
      } else {
        return { ...state, cartItems: [...state.cartItems, item] };
      }
    case CART_REMOVE_ITEM:
      return {
        ...state,
        cartItems: state.cartItems.filter((x) => x.product !== action.payload),
      };
    default:
      return state;
  }
};

export const toggleCartReducer =(state = { cartToggle: false }, action) => {
  switch (action.type) {
    case SHOW_CART:
      return { cartToggle: action.payload };
    case HIDE_CART:
      return { cartToggle: action.payload };
    default:
      return state;
  }
}