import React from 'react'
import BuyButton from '../../components/BuyButton';
import ProductDescriptionTableJS from '../../components/ProductDescriptionTableJS';
import logoSm from "../../img/general/ukemp-logo-sm.png";
import productImage from "../../img/products/mcs_200.png";

export default function MascaraCapilarSilver() {
  return (
    <div>
      <ProductDescriptionTableJS />      

      <div class="row product-landing bg-product-landing-mcs"></div>

      <section class="product-info-section">
        <div class="row">
          <div class="col-lg-6 order-2 order-lg-1">
            <div class="product-tabs">
              <div id="aboutExpander" class="tab">
                <h4 id="mcs-about" class="heading">Descripción</h4>
                <h4 class="indicator">
                  <span id="expandIconAbout">-</span>
                </h4>
              </div>
              <div id="aboutExpandable" class="bump">
                <p id="mcs-about-txt1">
                  Por su fórmula enriquecida con aceite de Cannabis, repara,
                  suaviza, hidrata y nutre el cabello mientras lo protege contra
                  el frizz. Con una sola aplicación se pueden neutralizar los
                  tonos amarillos no deseados en cabellos grises, rubios o platino
                  con resultados duraderos. ¡Hace que tu cabello esté vivo y
                  hermoso!
                </p>
                <p id="mcs-about-txt3">Para uso diario – Todo tipo de cabellos</p>
                <p id="mcs-about-txt4">Disponible en 200 mL</p>
                <p id="mcs-about-txt5" class="text-center">
                  <i>Calidad profesional, a tu alcance</i>
                </p>
              </div>

              <div id="benefitsExpander" class="tab">
                <h4 id="mcs-benefits" class="heading">
                  <span class="d-none d-sm-inline">Aceite de Semilla de</span>
                  Cannabis sativa L.
                </h4>
                <h4 class="indicator">
                  <span id="expandIconBenefits">+</span>
                </h4>
              </div>
              <div id="benefitsExpandable" class="bump">
                <p id="mcs-directions-txt1">
                  El aceite de cáñamo con el que formulamos se produce mediante
                  prensado en frio de las semillas de cáñamo para conservar todos
                  sus valiosos componentes intactos. Es considerado el más
                  nutritivo de todos los aceites disponibles.
                </p>
                <p id="mcs-directions-txt2">
                  La composición de ácidos grasos del aceite de semilla de cáñamo
                  alivia el cuero cabelludo de la sequedad y estimula su flujo
                  sanguíneo, permitiendo la llamada reposición de lípidos
                  fortaleciendo el cabello para darle el poder de combatir el
                  estrés diario.
                </p>
                <p id="mcs-directions-txt3">
                  El aceite de cáñamo garantiza un equilibrio estructural
                  perfecto, resultado: un cabello más fuerte y reestructurado
                  desde la raíz a las puntas!
                </p>
              </div>

              <div id="directionsExpander" class="tab">
                <h4 id="mcs-directions" class="heading">Indicaciones</h4>
                <h4 class="indicator">
                  <span id="expandIconDirections">+</span>
                </h4>
              </div>
              <div id="directionsExpandable" class="bump">
                <p id="mcs-directions-txt">
                  1. Lava tu cabello como de costumbre, y sécalo con una toalla
                  sin frotar.
                </p>
                <p>
                  2. Aplica la máscara mecha por mecha poco a poco. Ayúdate de un
                  peine para peinarlo bien y que penetre.
                </p>
                <p>
                  3. Deja reposar la máscara por lo menos 10 minutos, sin ningún
                  límite de tiempo. Cuanto más tiempo, mejor, y si puedes,
                  aplícate una toalla caliente, ya que el calor multiplica los
                  beneficios de la máscara.
                </p>
                <p>
                  4. Pasado este tiempo, elimina los restos de la máscara
                  enjuagando el cabello con agua tibia.
                </p>
                <p>
                  TIP: Puedes dejártelo toda la noche usando una gorra de baño
                  convencional.
                </p>
              </div>
            </div>

            <BuyButton productName="MascaraCapilarSilver" sizes={["200ml"]}></BuyButton>

          </div>
          <div class="col-lg-6 order-1 order-lg-2">
            <div class="row product-summary">
              <div class="col-sm-7 order-2 order-sm-1">
                <img class="logo" src={logoSm} alt="UKemp"/>
                <h1 id="mcs-pname">
                  MÁSCARA<i> RUBIO <span class="silver-text">SILVER</span></i>
                </h1>
                <h4 id="mcs-puse">Efecto Platinado</h4>
                <p id="mcs-summary">
                  Diseñado para revivir tu color rubio,
                  <b
                    > UKemp
                    <i
                      > MÁSCARA RUBIO <span class="silver-text">SILVER </span></i
                    ></b
                  >
                  con pigmentos violetas para cabellos cabellos rubios o grises,
                  con claritos, teñidos o iluminados, hidrata, nutre y da un
                  resultado sedoso y suave a tu cabello.
                </p>
              </div>
              <div class="col-sm-5 order-1 order-sm-2 text-center">
                <img
                  class="product-image"
                  style={{marginTop: 0}}
                  src={productImage}
                  alt="product"
                />
              </div>
            </div>
            <div class="row key-item-display">
              <div class="col-xl-6 key-item-left-col">
                <div class="key-item-left my-auto">
                  <h4 id="mcs-ingredient1">+ KERATINA</h4>
                  <p id="mcs-ingredient1-txt">
                    Repara las fibras del cabello, lo protege de agentes externos
                    y lo nutre desde la raíz dándole un aspecto totalmente
                    regenerado.
                  </p>
                </div>
              </div>
              <div class="col-xl-6 key-item-right-col">
                <div class="key-item-right my-auto">
                  <h4 id="mcs-ingredient2">+ MANZANILLA</h4>
                  <p id="mcs-ingredient2-txt">
                    La manzanilla promueve un cabello saludable y brillante por
                    sus propiedades antioxidantes, calmantes y antinflamatorias y
                    sus compuestos activos permiten aliviar la picazón del cuero
                    cabelludo seco que puede conducir a la caspa.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  )
}
