import React from 'react';
import swipe from '../img/general/swipe.png';
import image0 from '../img/about-us/about-us-0.jpg';
import image1 from '../img/about-us/about-us-1.jpeg';
import image2 from '../img/about-us/about-us-2.png';
import image3 from '../img/about-us/about-us-3.jpeg';
import image5 from '../img/about-us/about-us-5.jpeg';
import logo from '../img/general/ukemp-logo.png';
import $ from 'jquery';

export default function SobreNosotosPage() {

  $(function(){
    $(window).on('scroll', function() {
      $("#ingredients").css({"opacity": "1"});
    });
  });

  return (
    <div>
      <section className="banner-landing background-full-page bg-about-us-img"> 
        <div className="hero-title sansita-swashed d-none d-lg-block">
          <h1 id="title" className="">SOBRE</h1> 
          <h1 id="title" className="">NOSOTROS</h1>
        </div>
      </section>

      <div className="swipe-banner-img">
        <img src={swipe} alt="" />
      </div>

      <section className="about-content">
        <div className="content-page">
          <div className="row">
            <div className="col-lg-6 my-auto">
              <h1 className="d-lg-none sansita-swashed">SOBRE NOSOTROS</h1>
              <p id="text-1a">
                En <b className="ukemp-green">UKemp</b> nos destacamos por desarrollar productos comprometidos con la salud de nuestro plantea y utilizar derivados de Cannabis sativa L. en todas nuestras formulaciones.
              </p>
              <p id="text-1b">
                Desde hace muchos años nos enamoramos de una planta que cuenta con una miríada de beneficios increíbles y decidimos estudiarla para hacer de esta planta la actriz principal de nuestros cosméticos.
              </p>
            </div>
            <div className="col-lg-6 text-center my-auto">
              <img className="round-image" src={image0} alt="" />
            </div>
          </div>
        </div>
      </section>

      <h3 id="ingredients" className="ingredients-behind-text sansita-swashed">Cultivamos nuestros propios ingredientes!</h3>

      <div className="about-us-banner content-banner bg-static bg-about-us-banner">
      </div>

      <section className="about-content">
        <div className="content-page">
          <div className="row">
            <div className="col-lg-6 text-center my-auto">
              <img className="about-img" src={image2} alt="" />
            </div>
            <div className="col-lg-6 my-auto">
              <h4 id="subtitle-2">¿Por qué Cannabis sativa? ¡Te contamos!</h4>
              <p id="text-2a">
                El aceite de semilla de Cannabis sativa L. es un aceite verdoso claro con un ligero sabor a nuez que se extrae de las semillas de cáñamo (Cannabis sativa L.). 
              </p>
              <p id="text-2b">
                El secreto del aceite de semilla de Cannabis sativa L. es que contiene una mega dosis de importantes ácidos grasos poliinsaturados como omega-3, omega-6 y omega-9. Tu cuerpo no puede producir estos nutrientes naturalmente, pero los necesita ya sea a través de la dieta o la suplementación. Cuando se trata de nuestro pelo, estos omega-3, omega-6 y omega-9 pueden ayudan a controlar la pérdida de cabello y aumentar su densidad, según un estudio que rastreó la salud del cabello de más de 100 mujeres durante seis meses. 
              </p>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6 my-auto">
              <p id="text-3a">
                El aceite no solo contiene las tres omegas, está lleno de vitaminas D, E y magnesio, que ayudan a estimular el crecimiento de cabello nuevo. 
              </p>
              <p id="text-3b">
                El extracto de aceite de semilla de Cannabis también es alto en antioxidantes que protegen nuestras células del daño mediante la lucha contra los efectos de los radicales libres y el estrés oxidativo. Además, tiene abundancia de terpenos y flavonoides con propiedades anti-inflamatorias. 
              </p>
            </div>
            <div className="col-lg-6 text-center my-auto">
              <img className="round-image" src={image1} alt="" />
            </div>
          </div>
        </div>
      </section>

      <div className="about-us-banner content-banner bg-static bg-about-us-banner">
        <div className="overlay-banner"></div> 
      </div>

      <section className="about-content">
        <div className="content-page">
          <div className="row">
            <div className="col-lg-6 text-center my-auto">
              <img className="round-image" src={image3} alt="" />
            </div>
            <div className="col-lg-6 my-auto">
              <p id="text-4">
                Al igual que otros aceites, el aceite de semilla de Cannabis puede tener cualidades protectoras cuando se aplica directamente en el cabello y el cuero cabelludo. Hemos sabido desde hace mucho tiempo que el aceite puede evitar que demasiada agua u otras sustancias invadan el eje del cabello. Cuanto más poroso es el cabello por daños químicos, más vulnerable es al desgaste del agua. El aceite también ayuda a controlar el daño del cabello lubricando el eje y reduciendo la fuerza de peinado en las hebras empapadas. 
              </p>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6 my-auto">
              <p id="text-5a">
                Además, la vitamina E también juega un papel en la lucha contra los elementos y la lucha contra el daño. Esto creará una mayor elasticidad en el cabello mientras añade brillo. 
              </p>
              <p id="text-5b">
                Para el cabello seco, grueso o rizado, el aceite de semilla de Cannabis ayuda a fortalecer e hidratar el cabello que es más vulnerable al daño. Para un cuero cabelludo tanto seco como graso, los ácidos grasos y aminoácidos en el aceite de semilla de Cannabis proporcionan propiedades que ayudan a equilibrar el cuero cabelludo y a regular la producción de sebo, el aceite natural del cuerpo.
              </p>
            </div>
            <div className="col-lg-6 text-center my-auto">
              <img className="round-image" src={image5} alt="" />
            </div>
          </div>
          <div className="text-center" style={{padding:"80px 0"}}>
            <img className="about-ukemp" src={logo} alt="UKemp Logo" />
            <h5 id="slogan">Naturally You, Naturally Hemp</h5>
          </div>
        </div>
      </section>
    </div>
  )
}
